import { Observable } from '../core/_api';
/**
 * 
 * @param time
 */
export function debounceTime(time) {
    return function (source) {
        return new Observable(subscriber => {
            let timer;
            const sub = source.subscribe({
                next(v) {
                    clearTimeout(timer);
                    timer = setTimeout(function () {
                        subscriber.next(v);
                    }, time);
                },
                error(err) {
                    subscriber.error(err);
                },
                complete() {
                    subscriber.complete();
                }
            });
            return function () {
                clearTimeout(timer);
                sub.unsubscribe();
            };
        });
    };
}
