import { Observable } from '../core/_api';
/**
 * 
 * @param time 
 */
export function sampleTime(time) {
    return function (source) {
        return new Observable(subscriber => {
            let canPublish = true;
            let timer;
            let value;
            let isComplete = false;
            let hasError = false;
            const sub = source.subscribe({
                next(v) {
                    value = v;
                    if (canPublish) {
                        canPublish = false;
                        timer = setTimeout(() => {
                            canPublish = true;
                            subscriber.next(value);
                            if (isComplete) {
                                subscriber.complete();
                            }
                        }, time);
                    }
                },
                error(err) {
                    hasError = true;
                    if (sub) {
                        sub.unsubscribe();
                    }
                    subscriber.error(err);
                },
                complete() {
                    isComplete = true;
                }
            });
            if (hasError) {
                sub.unsubscribe();
            }
            return function () {
                clearTimeout(timer);
                sub.unsubscribe();
            };
        });
    };
}
