import { Observable } from '../core/_api';
/**
 * 
 */
export function distinctUntilChanged(comparator) {
    return function (source) {
        return new Observable(subscriber => {
            let lastValue = {};
            let isFirst = true;
            return source.subscribe({
                next(value) {
                    if (isFirst) {
                        isFirst = false;
                        lastValue = value;
                        subscriber.next(value);
                        return;
                    }
                    if (comparator) {
                        const b = comparator(lastValue, value);
                        lastValue = value;
                        if (b) {
                            subscriber.next(value);
                        }
                    }
                    else if (value !== lastValue) {
                        lastValue = value;
                        subscriber.next(value);
                    }
                },
                error(err) {
                    subscriber.error(err);
                },
                complete() {
                    subscriber.complete();
                }
            });
        });
    };
}
