import { Observable } from '../core/_api';
/**
 *  DOM 
 * @param source  DOM 
 * @param type 
 */
export function fromEvent(source, type) {
    const subscribers = [];
    function listenFn(event) {
        return [...subscribers].forEach(subscriber => {
            subscriber.next(event);
        });
    }
    return new Observable(subscriber => {
        if (subscribers.length === 0) {
            source.addEventListener(type, listenFn);
        }
        subscribers.push(subscriber);
        return function () {
            const index = subscribers.indexOf(subscriber);
            if (index > -1) {
                subscribers.splice(index, 1);
            }
            if (subscribers.length === 0) {
                source.removeEventListener(type, listenFn);
            }
        };
    });
}
