export const defaultOptions:EditorOptions = {
    editingStyleSheets: [
        `[style*=color]:not([style*=background-color])
            a {color: inherit;}`,
        `a {text-decoration: underline; color: #449fdb; cursor: text;}`,
        `[textbus-editable=off] > * {
            user-select: text;
            cursor: default;
          }`
    ],

    //
    componentLoaders: defaultComponentLoader,
    ///
    formatLoaders:defaultFormatLoaders,
    plugins: [
        //
        new Toolbar(defaultToolFactories),
        new LinkJumpTipPlugin(),
        new OutlinesPlugin(),
    ],
    providers:[
        {
            provide:UIControlPanel,
            useFactory(layout,editorController,selection,editableDocument){
                return new UIControlPanel(layout,editorController,selection,editableDocument)
            },
            deps:[Layout,EditorController,Selection,EDITABLE_DOCUMENT]
        }
    ],
    //content:'<p>ddd</p>',
    placeholder: ""
};